import { Col, DatePicker, Form, message, Modal, Row, Select } from "antd";
import { CloseOutlined } from "@ant-design/icons";
import { COMMON_MESSAGES, DATE_FORMAT, VALIDATION_FAILED } from "constants/CommonConstants";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import React, { useState } from "react";
import { useSelector } from "react-redux";
import employeeService from "services/EmployeeService";
import { CONFIRM_MESSAGE } from "../../../../constants/ManageAccounts";
import { EMPLOYEE_LABELS, EMP_FORM_FIELDS } from "../../../../constants/ManageEmployeeConstants";

const UpdateSubStatusForm = ({ handleOk, handleCancel, visible, employeeToEdit, handleRefresh }) => {  
  const { Option } = Select;
  const employeeSubStatusList = useSelector(state => state.myTeams.masterData.EmployeeSubStatus);
  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");

  const [employeeSubStatus, setEmployeSubStatus] = useState(
    employeeToEdit?.empSubStatus
      ? employeeToEdit?.empSubStatus
      : employeeToEdit?.subStatus
      ? employeeToEdit?.subStatus
      : null,
  );
  const [empSubStatusStartDate, setEmpSubStatusStartDate] = useState(null);
  const [form] = Form.useForm();
  const { confirm } = Modal;
  const getMessage = useIntlMessage();
  const formItemLayout = {
    labelCol: { span: 10 },
    wrapperCol: { span: 14 },
  };

  const initialData = {
    employeeId: employeeToEdit?.employeeId,
    subStatus: employeeToEdit?.empSubStatus || employeeToEdit?.subStatus,
    toDate: employeeToEdit?.empSubStatusEndDate
      ? moment(employeeToEdit?.empSubStatusEndDate)
      : employeeToEdit?.toDate
      ? moment(employeeToEdit?.toDate)
      : null,
    fromDate: employeeToEdit?.empSubStatusStartDate
      ? moment(employeeToEdit?.empSubStatusStartDate)
      : employeeToEdit?.fromDate
      ? moment(employeeToEdit?.fromDate)
      : null,
  };
  function showConfirm() {
    confirm({
      title: CONFIRM_MESSAGE.value,
      centered: true,
      onOk() {
        handleCancel();
      },
      onCancel() {},
    });
  }
  const HandleSubmit = () => {
    form
      .validateFields()
      .then(values => {
        saveData({...values, employeeId: employeeToEdit?.employeeId});
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };
  const handleApiResponse = response => {
    if (response?.statusCode === 901 || response?.statusCode === 906) {
      message.success(
        response.statusCode === 906
          ? getMessage(EMPLOYEE_LABELS.EMPLOYEE_UPDATE_SUCCESS)
          : getMessage(EMPLOYEE_LABELS.EMPLOYEE_CREATE_SUCCESS),
      );
      handleOk();
      handleRefresh();
      handleCancel();
      form.resetFields();
    } else {
      setErrorMessage(response?.message || "");
    }
  };
  const saveData = async values => {
    try {
      setLoading(true);
      let response;
      if (isEditMode()) {
        response = await employeeService.updateSubStatus(values);
        if (response?.statusCode === 400 || response?.statusCode === 401) {
          response = await employeeService.createSubStatus(values);
        }
      }
      handleApiResponse(response);
    } catch (e) {
      console.error("Unable to save employee data", e);
    } finally {
      setLoading(false);
    }
  };
  const onCancelHandler = () => {
    if (form.isFieldsTouched()) {
      showConfirm();
    } else {
      handleCancel();
    }
  };

  // Memoized version of making fields disabled.
  const isEditMode = React.useCallback(() => {
    return employeeToEdit && true;
  }, [employeeToEdit]);

  return (
    <>
      <Modal
        title={
          isEditMode() ? getMessage(EMPLOYEE_LABELS.MODAL_TITLE_UPDATE) : getMessage(EMPLOYEE_LABELS.MODAL_TITLE_ADD)
        }
        visible={visible}
        centered
        width="33%"
        okText={isEditMode() ? getMessage(EMPLOYEE_LABELS.UPDATE_TEXT) : getMessage(EMPLOYEE_LABELS.ADD_TEXT)}
        onOk={HandleSubmit}
        onCancel={onCancelHandler}
        okButtonProps={{ loading: loading, id: "add-emp-addBtn" }}
        cancelButtonProps={{ id: "add-emp-cancelBtn" }}
        closeIcon={<CloseOutlined id="add-emp-close-modal-btn" />}
      >
        <Form
          {...formItemLayout}
          layout={"horizontal"}
          form={form}
          labelAlign="left"
          initialValues={employeeToEdit ? initialData : { role: "Employee" }}
          requiredMark=""
        >
              <Row gutter={24}>
                <Col className="mb-1" xs={24}>
                  <Form.Item
                    label={getMessage(EMP_FORM_FIELDS.EMPLOYEE_SUB_STATUS.label)}
                    name={EMP_FORM_FIELDS.EMPLOYEE_SUB_STATUS.name}
                  >
                      <Select
                        placeholder={getMessage(EMP_FORM_FIELDS.EMPLOYEE_SUB_STATUS.label)}
                        allowClear
                        onChange={status => setEmployeSubStatus(status)}
                        value={employeeToEdit?.subStatus}
                      >
                        {employeeSubStatusList?.map(status => {
                          return (
                            <Option key={status} value={status}>
                              {status}
                            </Option>
                          );
                        })}
                      </Select>
                  </Form.Item>
                </Col>
                {employeeSubStatus && (
                  <Col className="mb-1" xs={24}>
                    <Form.Item
                      label={
                        <div className="text-wrap">{`${employeeSubStatus} ${getMessage(
                          EMP_FORM_FIELDS.SUB_STATUS_START_DATE.label,
                        )}`}</div>
                      }
                      name={EMP_FORM_FIELDS.SUB_STATUS_START_DATE.name}
                      rules={[
                        {
                          required: isEditMode() ? false : true,
                          message: getMessage(
                            COMMON_MESSAGES.REQIRED_PREFIX,
                            EMP_FORM_FIELDS.SUB_STATUS_START_DATE.label,
                          ),
                        },
                      ]}
                    >
                      <DatePicker
                        onSelect={value => setEmpSubStatusStartDate(value)}
                        format={DATE_FORMAT}
                        value={employeeToEdit?.fromDate}
                        id="emp-date-of-joining"
                        className="w-100"
                      />
                    </Form.Item>
                  </Col>
                )}
                {employeeSubStatus && (
                  <Col className="mb-1" xs={24}>
                    <Form.Item
                      label={
                        <div className="text-wrap">{`${employeeSubStatus} ${getMessage(
                          EMP_FORM_FIELDS.SUB_STATUS_END_DATE.label,
                        )}`}</div>
                      }
                      name={EMP_FORM_FIELDS.SUB_STATUS_END_DATE.name}
                      rules={[
                        {
                          required: isEditMode() ? false : true,
                          message: getMessage(
                            COMMON_MESSAGES.REQIRED_PREFIX,
                            EMP_FORM_FIELDS.SUB_STATUS_END_DATE.label,
                          ),
                        },
                      ]}
                    >
                      <DatePicker
                        format={DATE_FORMAT}
                        value={employeeToEdit?.toDate}
                        className="w-100"
                      />
                    </Form.Item>
                  </Col>
                )}
              </Row>
          <Col>
            {errorMessage && (
              <div className="text-left my-3" style={{ color: "red" }}>
                * {errorMessage}
              </div>
            )}
          </Col>
        </Form>
      </Modal>
    </>
  );
};

export default UpdateSubStatusForm;
